body {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f0f0;
  margin: 0;
}

h1 {
  margin-bottom: 20px;
}

button {
  padding: 10px 20px;
  margin: 5px;
  font-size: 16px;
  cursor: pointer;
}

.result {
  margin-top: 20px;
  font-size: 20px;
}

#score {
  margin-top: 10px;
  font-size: 18px;
}

.button-container {
  display: flex;
  gap: 10px;
}

.image-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 30px;
}

img {
  width: 100px; /* 이미지 크기 조정 */
  height: 100px;
}

.image-label {
  text-align: center;
  margin-top: 5px;
  font-weight: bold;
}
